'use strict';

layui.define(['layer', 'table', 'form', 'laypage', 'commonTpl'], function (exports) {
    var layer = layui.layer;
    var table = layui.table;
    var form = layui.form;
    var laypage = layui.laypage;
    var commonTpl = layui.commonTpl;
    //
    var active_data;
    var getData = function getData(query, callback) {
        $.get('/Admin/Sys/roleList', query, function (res) {
            res = $.parseJSON(res);
            if (res.error === 0) {
                active_data = res.data;
                callback(res.data);
            } else {
                layer.tips(res.msg, '.modal-search-btn');
            }
        });
    };
    var renderTable = function renderTable(data) {
        table.render({
            elem: '.select-modal-table',
            data: data.list,
            page: false,
            limit: data.pageSize,
            cellMinWidth: 160,
            height: '320',
            cols: [[{ field: 'checkbox', width: 50, fixed: 'left', templet: '<div><input class="select-modal-checkbox" lay-filter="c_check" type="checkbox" name="" lay-skin="primary"></div>' }, { field: 'role_id', title: 'ID', width: 100 }, { field: 'role_name', title: '角色名称', width: 160 }, { field: 'role_desc', title: '角色描述', width: 160 }, { field: 'role_level', title: '角色等级', width: 160 }, { field: 'is_disabled', title: '是否禁用', width: 160, templet: function templet(d) {
                    return commonTpl.oneZero(d.is_disabled);
                } }]]
        });
        form.on('checkbox(c_check)', function (data) {
            var $checkboxs = $('.select-modal-checkbox');
            $checkboxs.prop('checked', false);
            $(data.elem).prop('checked', true);
            form.render('checkbox');
        });
    };
    var renderPage = function renderPage(data) {
        laypage.render({
            elem: $('.layui-layer-content').find('.select-modal-page')[0],
            limit: data.pageSize,
            count: data.count,
            curr: data.page,
            layout: ['prev', 'page', 'next'],
            jump: function jump(obj, first) {
                if (!first) {
                    getData({
                        page: obj.curr,
                        pageSize: data.pageSize
                    }, function (data) {
                        renderTable(data);
                    });
                }
            }
        });
    };
    exports("selectRole", {
        start: function start(callback) {
            layer.open({
                title: '选择角色',
                type: 0,
                area: ['640px', '540px'],
                content: '<div class="select-modal-container"> ' + '<div class="select-modal-box"> ' + '<div class="select-search-container">' + '<input class="layui-input modal-search-input" type="text" placeholder="输入角色编号检索">' + '<div class="layui-btn-group"> ' + '<button class="layui-btn modal-search-btn">搜索</button> ' + '<button class="layui-btn layui-btn-primary modal-clear-btn">清空</button> ' + '</div>' + '</div>' + '<table lay-filter="select-modal-table" class="select-modal-table"></table> ' + '<div class="select-modal-page"></div> ' + '</div> ' + '</div>',
                success: function success() {
                    $('.modal-search-btn').click(function () {
                        var query = {};
                        var search = $('.modal-search-input').val().trim();
                        if (search) {
                            query['role_id'] = search;
                        }
                        getData(query, function (data) {
                            renderTable(data);
                            renderPage(data);
                        });
                    });
                    $('.modal-clear-btn').on('click', function () {
                        $('.modal-search-input').val('');
                        getData({}, function (data) {
                            renderTable(data);
                            renderPage(data);
                        });
                    });
                    getData({}, function (data) {
                        renderTable(data);
                        renderPage(data);
                    });
                },
                yes: function yes(index, layero) {
                    var $checked = $('.layui-layer-content .select-modal-checkbox:checked');
                    if ($checked.length) {
                        var eq = $checked.parent().parent().parent().index();
                        var data = active_data.list[eq];
                        callback(data);
                        layer.close(index);
                    } else {
                        layer.tips('请勾选需要操作的数据', '.layui-layer-btn0');
                    }
                }
            });
        },
        initInput: function initInput(selector, callback, need) {
            var _this = this;
            selector = selector ? selector : '.jl-select-role';
            need = need ? need : 'role_name';
            $(selector).click(function () {
                var $this = $(this);
                _this.start(function (data) {
                    $this.val(data[need]);
                    if (callback) {
                        callback(data, $this);
                    }
                });
            });
        }
    });
});